@import url(https://fonts.googleapis.com/css2?family=Be+Vietnam+Pro:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Be+Vietnam+Pro:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Be+Vietnam+Pro:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

#card-group{display:flex;flex-wrap:wrap;align-items:center;justify-content:center}
#card{font-family:"Be Vietnam Pro",sans-serif;width:300px;height:auto;box-shadow:rgba(50,50,93,.25) 0px 30px 60px -12px,rgba(0,0,0,.3) 0px 18px 36px -18px;margin:14px;border-radius:6px;overflow:hidden}#card h4{margin:1px}@media only screen and (max-width: 650px){#card{width:400px}}#card p{margin-top:5px;font-size:10px}#card img{width:100%;height:80%}#card:hover{cursor:pointer}#card:hover h4{text-decoration:underline}#card #console_name{height:30px;width:30px;margin-left:auto;margin-right:auto;margin-bottom:5px;display:flex;align-items:center;justify-content:center;border-radius:6px;background-color:#08b908}
#input{width:200px;padding:5px;margin:10px;border-radius:10px;box-shadow:rgba(0,0,0,.17) 0px -23px 25px 0px inset,rgba(0,0,0,.15) 0px -36px 30px 0px inset,rgba(0,0,0,.1) 0px -79px 40px 0px inset,rgba(0,0,0,.06) 0px 2px 1px,rgba(0,0,0,.09) 0px 4px 2px,rgba(0,0,0,.09) 0px 8px 4px,rgba(0,0,0,.09) 0px 16px 8px,rgba(0,0,0,.09) 0px 32px 16px}#input input{background-color:transparent;border:0;outline:none}.header{font-family:"Be Vietnam Pro",sans-serif;display:flex;align-items:center;justify-content:space-around}#heading{cursor:pointer}@media only screen and (max-width: 450px){.header{display:block}#input{margin-left:auto;margin-right:auto}}
#consoles_list{height:100vh;width:300px;position:fixed;left:0;top:0;bottom:0;overflow-y:scroll;-webkit-transform:translateX(-300px);transform:translateX(-300px);background-color:#fff;transition:all 200ms ease}#consoles_list div{margin:5px;padding:10px;text-align:left;width:250px;border:1px solid #000;border-radius:6px;transition:all 200ms ease}#consoles_list div:hover{box-shadow:rgba(50,50,93,.25) 0px 30px 60px -12px,rgba(0,0,0,.3) 0px 18px 36px -18px;border:1px solid rgba(0,0,0,.432)}#consoles_list::-webkit-scrollbar{background-color:transparent}#console_list::-webkit-scrollbar-thumb{background-color:blue}
#poster{font-family:"Be Vietnam Pro",sans-serif;height:400px;width:300px;border-radius:6px;float:left;margin:10px}#details__div{font-family:"Be Vietnam Pro",sans-serif;display:flex;align-items:center;justify-content:space-around}@media only screen and (max-width: 710px){#details__div{display:block;align-items:center;justify-content:center}#details__div #poster{float:none}#details__div #text{margin-left:auto;margin-right:auto}}#text{font-family:"Be Vietnam Pro",sans-serif;display:block;margin-right:auto;text-align:center;align-items:center;justify-content:center;margin-left:200px}#download{border:0;outline:none;display:flex;align-items:center;padding:10px;margin-bottom:10px;justify-content:space-around;margin-left:auto;margin-right:auto;cursor:pointer;font-weight:800;background-color:transparent;border-radius:6px;box-shadow:rgba(0,0,0,.25) 0px 54px 55px,rgba(0,0,0,.12) 0px -12px 30px,rgba(0,0,0,.12) 0px 4px 6px,rgba(0,0,0,.17) 0px 12px 13px,rgba(0,0,0,.09) 0px -3px 5px}#download *{margin:2px}
